<template>
  <div>
    <!-- <button @click="back()">返回</button> -->
    <v-back></v-back>
    <p>------------------1、router-link----------------</p>
    <router-link to="/movie" v-if="status==0">电影</router-link>
    <router-link to="/food" v-else>外卖</router-link>
    <p>--------------2、$router.push("/path")------------</p>
    <button @click="toMovie()" v-if="status==0">电影</button>
    <button @click="toFood()" v-else>外卖</button>
    <p>------------3、$router.replace("/path")----------</p>
    <button @click="replaceMovie()" v-if="status==0">电影</button>
    <button @click="replaceFood()" v-else>外卖</button>

    <hr>
    <p>1、如果跳转没有条件，就可以使用router-link</p>
    <p>2、push是添加一条新的历史记录；replace是新的历史记录替换当前的历史记录</p>
    <p>3、router-link采用的是添加一条新的历史记录</p>
    <p>4、编程式导航push、replace、go()</p>
  </div>
</template>
<script>
  import vBack from '../common/vBack.vue'
  export default {
    components: {
      vBack
    },
    data() {
      return {
        status:sessionStorage.getItem("status")
      }
    },
    methods: {
      toMovie() {
        this.$router.push("/movie")
      },
      toFood() {
        this.$router.push("/food")
      },
      replaceMovie() {
        this.$router.replace("/movie")
      },
      replaceFood() {
        this.$router.replace("/food")
      },
      back() {
        this.$router.go(-1)
      }
    },
    watch: {},
    mounted() {
      console.log('home进来了');
    },
    beforeDestroy() {
      console.log('home销毁了');
    }
  }

</script>
<style>
</style>



